"use client";
import React, { useState, useMemo } from "react";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";
import { Modal } from "@/components/ui/modal";
import { Table, TableBody, TableCell, TableHeader, TableRow } from "@/components/ui/table";
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from "recharts";
import { Edit3, Trash2, CheckCircle, XCircle } from "lucide-react";

interface Timesheet {
    id: number;
    date: string;
    regularHours: number;
    overtimeHours: number;
    notes: string;
    status: "Pending" | "Approved" | "Rejected";
    selected?: boolean;
}

const initialTimesheets: Timesheet[] = [
    { id: 1, date: "2025-12-25", regularHours: 8, overtimeHours: 2, notes: "Worked on project X", status: "Pending" },
    { id: 2, date: "2025-12-26", regularHours: 7, overtimeHours: 1, notes: "", status: "Pending" },
    { id: 3, date: "2025-12-27", regularHours: 8, overtimeHours: 0, notes: "", status: "Approved" },
    { id: 4, date: "2025-12-28", regularHours: 6, overtimeHours: 3, notes: "Urgent task", status: "Pending" },
    { id: 5, date: "2025-12-29", regularHours: 8, overtimeHours: 1, notes: "", status: "Rejected" },
];

export default function TimesheetPage() {
    const [timesheets, setTimesheets] = useState<Timesheet[]>(initialTimesheets);
    const [modalOpen, setModalOpen] = useState(false);
    const [editingTimesheet, setEditingTimesheet] = useState<Timesheet | null>(null);
    const [date, setDate] = useState("");
    const [regularHours, setRegularHours] = useState(8);
    const [overtimeHours, setOvertimeHours] = useState(0);
    const [notes, setNotes] = useState("");
    const [currentPage, setCurrentPage] = useState(1);
    const timesheetsPerPage = 5;

    // Pagination
    const totalPages = Math.ceil(timesheets.length / timesheetsPerPage);
    const displayedTimesheets = timesheets.slice(
        (currentPage - 1) * timesheetsPerPage,
        currentPage * timesheetsPerPage
    );

    // Chart data
    const chartData = useMemo(() => {
        const weekDays = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
        return weekDays.map((day, idx) => {
            const ts = timesheets.find(t => new Date(t.date).getDay() === idx);
            return { day, regular: ts ? ts.regularHours : 0, overtime: ts ? ts.overtimeHours : 0 };
        });
    }, [timesheets]);

    const totalHours = useMemo(() => timesheets.reduce((sum, t) => sum + t.regularHours + t.overtimeHours, 0), [timesheets]);
    const averageHours = useMemo(() => (timesheets.length ? totalHours / timesheets.length : 0), [totalHours, timesheets.length]);

    // Modal functions
    const openModalForNew = () => {
        setEditingTimesheet(null);
        setDate("");
        setRegularHours(8);
        setOvertimeHours(0);
        setNotes("");
        setModalOpen(true);
    };

    const openModalForEdit = (t: Timesheet) => {
        setEditingTimesheet(t);
        setDate(t.date);
        setRegularHours(t.regularHours);
        setOvertimeHours(t.overtimeHours);
        setNotes(t.notes);
        setModalOpen(true);
    };

    const closeModal = () => setModalOpen(false);

    const handleSaveTimesheet = () => {
        if (!date) return;
        if (editingTimesheet) {
            setTimesheets(timesheets.map(t =>
                t.id === editingTimesheet.id
                    ? { ...t, date, regularHours, overtimeHours, notes }
                    : t
            ));
        } else {
            const newTimesheet: Timesheet = {
                id: timesheets.length + 1,
                date,
                regularHours,
                overtimeHours,
                notes,
                status: "Pending"
            };
            setTimesheets([newTimesheet, ...timesheets]);
        }
        setModalOpen(false);
    };

    const handleDelete = (id: number) => setTimesheets(timesheets.filter(t => t.id !== id));

    const toggleSelect = (id: number) => {
        setTimesheets(timesheets.map(t => t.id === id ? { ...t, selected: !t.selected } : t));
    };

    const bulkApproveReject = (action: "Approved" | "Rejected") => {
        setTimesheets(timesheets.map(t => t.selected ? { ...t, status: action, selected: false } : t));
    };

    const handleApproveReject = (id: number, action: "Approved" | "Rejected") => {
        setTimesheets(timesheets.map(t => t.id === id ? { ...t, status: action } : t));
    };

    return (
        <div className="p-6">
            <PageBreadcrumb pageTitle="Timesheet" />

            <div className="flex justify-between mb-4">
                <h2 className="text-xl font-semibold"></h2>
                <button
                    className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
                    onClick={openModalForNew}
                >
                    New Timesheet
                </button>
            </div>

            {/* Summary */}
            <div className="mb-6 grid grid-cols-1 sm:grid-cols-2 gap-4">
                <div className="p-4 border rounded-xl bg-white shadow">
                    <h3 className="text-sm font-medium text-gray-500">Average Hours/Day</h3>
                    <p className="text-2xl font-bold">{averageHours.toFixed(2)} hrs</p>
                </div>
                <div className="p-4 border rounded-xl bg-white shadow">
                    <h3 className="text-sm font-medium text-gray-500">Total Overtime</h3>
                    <p className="text-2xl font-bold">{timesheets.reduce((sum, t) => sum + t.overtimeHours, 0)} hrs</p>
                </div>
            </div>

            {/* Weekly Chart */}
            <div className="mb-6 p-4 border rounded-xl bg-white shadow h-64">
                <h3 className="text-lg font-semibold mb-2 text-gray-800 dark:text-white">This Week Hours</h3>
                <ResponsiveContainer width="100%" height="100%">
                    <BarChart data={chartData} margin={{ top: 20, right: 30, left: 0, bottom: 5 }}>
                        <CartesianGrid strokeDasharray="3 3" />
                        <XAxis dataKey="day" />
                        <YAxis />
                        <Tooltip />
                        <Bar dataKey="regular" stackId="a" fill="#3b82f6" />
                        <Bar dataKey="overtime" stackId="a" fill="#f97316" />
                    </BarChart>
                </ResponsiveContainer>
            </div>

            {/* Bulk actions */}
            <div className="mb-2 flex gap-2">
                <button className="px-3 py-1 bg-green-600 text-white rounded hover:bg-green-700" onClick={() => bulkApproveReject("Approved")}>Approve Selected</button>
                <button className="px-3 py-1 bg-red-600 text-white rounded hover:bg-red-700" onClick={() => bulkApproveReject("Rejected")}>Reject Selected</button>
            </div>

            {/* Timesheet Table */}
            <div className="overflow-hidden rounded-xl border border-gray-200 bg-white p-6">
                <div className="max-w-full overflow-x-auto">
                    <Table>
                        <TableHeader className="bg-gray-50 dark:bg-gray-800">
                            <TableRow>
                                <TableCell isHeader>
                                    <input type="checkbox" onChange={e => setTimesheets(timesheets.map(t => ({ ...t, selected: e.target.checked })))} />
                                </TableCell>
                                <TableCell isHeader>Date</TableCell>
                                <TableCell isHeader className="text-right">Regular Hours</TableCell>
                                <TableCell isHeader className="text-right">Overtime Hours</TableCell>
                                <TableCell isHeader>Notes</TableCell>
                                <TableCell isHeader>Status</TableCell>
                                <TableCell isHeader>Actions</TableCell>
                            </TableRow>
                        </TableHeader>
                        <TableBody className="divide-y divide-gray-100 dark:divide-white/[0.05]">
                            {displayedTimesheets.map(t => (
                                <TableRow key={t.id} className={t.status === "Approved" ? "bg-green-50" : t.status === "Rejected" ? "bg-red-50" : "bg-white"}>
                                    <TableCell>
                                        <input type="checkbox" checked={!!t.selected} onChange={() => toggleSelect(t.id)} />
                                    </TableCell>
                                    <TableCell>{t.date}</TableCell>
                                    <TableCell className="text-right">{t.regularHours}</TableCell>
                                    <TableCell className="text-right">{t.overtimeHours}</TableCell>
                                    <TableCell>{t.notes}</TableCell>
                                    <TableCell>{t.status}</TableCell>
                                    <TableCell className="flex justify-center gap-2">
                                        <Edit3 className="cursor-pointer text-blue-600" onClick={() => openModalForEdit(t)} />
                                        <Trash2 className="cursor-pointer text-red-600" onClick={() => handleDelete(t.id)} />
                                        {t.status === "Pending" && (
                                            <>
                                                <CheckCircle className="cursor-pointer text-green-600" onClick={() => handleApproveReject(t.id, "Approved")} />
                                                <XCircle className="cursor-pointer text-red-600" onClick={() => handleApproveReject(t.id, "Rejected")} />
                                            </>
                                        )}
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </div>

                {/* Pagination */}
                {totalPages > 1 && (
                    <div className="mt-4 flex justify-center gap-2">
                        <button onClick={() => setCurrentPage(Math.max(currentPage - 1, 1))} className="px-3 py-1 border rounded" disabled={currentPage === 1}>Prev</button>
                        {[...Array(totalPages)].map((_, idx) => (
                            <button key={idx} onClick={() => setCurrentPage(idx + 1)} className={`px-3 py-1 border rounded ${currentPage === idx + 1 ? "bg-blue-600 text-white" : ""}`}>{idx + 1}</button>
                        ))}
                        <button onClick={() => setCurrentPage(Math.min(currentPage + 1, totalPages))} className="px-3 py-1 border rounded" disabled={currentPage === totalPages}>Next</button>
                    </div>
                )}
            </div>

            {/* Modal for Add/Edit */}
            <Modal isOpen={modalOpen} onClose={closeModal} className="max-w-md p-6">
                <h2 className="text-lg font-semibold mb-4">{editingTimesheet ? "Edit Timesheet" : "New Timesheet"}</h2>
                <div className="space-y-4">
                    <div>
                        <label className="block text-sm font-medium mb-1">Date</label>
                        <input type="date" className="w-full border rounded px-3 py-2" value={date} onChange={e => setDate(e.target.value)} />
                    </div>
                    <div>
                        <label className="block text-sm font-medium mb-1">Regular Hours</label>
                        <input type="number" className="w-full border rounded px-3 py-2" value={regularHours} onChange={e => setRegularHours(Number(e.target.value))} />
                    </div>
                    <div>
                        <label className="block text-sm font-medium mb-1">Overtime Hours</label>
                        <input type="number" className="w-full border rounded px-3 py-2" value={overtimeHours} onChange={e => setOvertimeHours(Number(e.target.value))} />
                    </div>
                    <div>
                        <label className="block text-sm font-medium mb-1">Notes</label>
                        <input type="text" className="w-full border rounded px-3 py-2" value={notes} onChange={e => setNotes(e.target.value)} placeholder="Enter notes or task details" />
                    </div>
                    <div className="flex justify-end gap-2">
                        <button className="px-4 py-2 bg-gray-300 rounded hover:bg-gray-400" onClick={closeModal}>Cancel</button>
                        <button className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700" onClick={handleSaveTimesheet}>{editingTimesheet ? "Update" : "Add"}</button>
                    </div>
                </div>
            </Modal>
        </div>
    );
}
